<template>
	<view>
		<view class="container" id="poster" @success="onCreateSuccess" @fail="onCreateFail">
			<canvas canvas-id='canvasid' :class="[canvas , debug ? 'debug' : 'pro']" :style="{width: pxWidth + 'px', height: pxHeight + 'px'}">
			</canvas>
		</view>
	</view>

</template>

<script>
	import { main, handle, helper} from './unit.js'
	export default {
		data() {
			return {
				pxWidth:'',
				pxHeight:''
			}
		},
		props: {
			config: {
				type: Object,
				value: {},
			},
			preload: { // 是否预下载图片资源
				type: Boolean,
				value: false,
			},
			hideLoading: { // 是否隐藏loading
				type: Boolean,
				value: true,
			}
		},
		onLoad() {
			const sysInfo = wx.getSystemInfoSync();
			const screenWidth = sysInfo.screenWidth;
			this.factor = screenWidth / 750;
		},
		onReady() {
			if (this.preload) {
				const poster = this.selectComponent('#poster');
				this.downloadStatus = 'doing';
				poster.downloadResource(this.config).then(() => {
					this.downloadStatus = 'success';
					this.trigger('downloadSuccess');
				}).catch((e) => {
					this.downloadStatus = 'fail';
					this.trigger('downloadFail', e);
				});
			}
		},
		methods: Object.assign({
			/**
			 * 计算画布的高度
			 * @param {*} config
			 */
			getHeight(config) {
				const getTextHeight = (text) => {
					let fontHeight = text.lineHeight || text.fontSize;
					let height = 0;
					if (text.baseLine === 'top') {
						height = fontHeight;
					} else if (text.baseLine === 'middle') {
						height = fontHeight / 2;
					} else {
						height = 0;
					}
					return height;
				}
				const heightArr = [];
				(config.blocks || []).forEach((item) => {
					heightArr.push(item.y + item.height);
				});
				(config.texts || []).forEach((item) => {
					let height;
					if (Object.prototype.toString.call(item.text) === '[object Array]') {
						item.text.forEach((i) => {
							height = getTextHeight({ ...i,
								baseLine: item.baseLine
							});
							heightArr.push(item.y + height);
						});
					} else {
						height = getTextHeight(item);
						heightArr.push(item.y + height);
					}
				});
				(config.images || []).forEach((item) => {
					heightArr.push(item.y + item.height);
				});
				(config.lines || []).forEach((item) => {
					heightArr.push(item.startY);
					heightArr.push(item.endY);
				});
				const sortRes = heightArr.sort((a, b) => b - a);
				let canvasHeight = 0;
				if (sortRes.length > 0) {
					canvasHeight = sortRes[0];
				}
				if (config.height < canvasHeight || !config.height) {
					return canvasHeight;
				} else {
					return config.height;
				}
			},
			create(config) {
				this.ctx = wx.createCanvasContext('canvasid', this);
		
				this.pixelRatio = config.pixelRatio || 1;
				const height = this.getHeight(config);
				this.initCanvas(config.width, height, config.debug)
					.then(() => {
						// 设置画布底色
						if (config.backgroundColor) {
							this.ctx.save();
							this.ctx.setFillStyle(config.backgroundColor);
							this.ctx.fillRect(0, 0, this.toPx(config.width), this.toPx(height));
							this.ctx.restore();
						}
						const {
							texts = [], images = [], blocks = [], lines = []
						} = config;
						const queue = this.drawArr
							.concat(texts.map((item) => {
								item.type = 'text';
								item.zIndex = item.zIndex || 0;
								return item;
							}))
							.concat(blocks.map((item) => {
								item.type = 'block';
								item.zIndex = item.zIndex || 0;
								return item;
							}))
							.concat(lines.map((item) => {
								item.type = 'line';
								item.zIndex = item.zIndex || 0;
								return item;
							}));
						// 按照顺序排序
						queue.sort((a, b) => a.zIndex - b.zIndex);
		
						queue.forEach((item) => {
							if (item.type === 'image') {
								this.drawImage(item)
							} else if (item.type === 'text') {
								this.drawText(item)
							} else if (item.type === 'block') {
								this.drawBlock(item)
							} else if (item.type === 'line') {
								this.drawLine(item)
							}
						});
		
						const res = wx.getSystemInfoSync();
						const platform = res.platform;
						let time = 0;
						if (platform === 'android') {
							// 在安卓平台，经测试发现如果海报过于复杂在转换时需要做延时，要不然样式会错乱
							time = 300;
						}
						this.ctx.draw(false, () => {
							setTimeout(() => {
								wx.canvasToTempFilePath({
									canvasId: 'canvasid',
									success: (res) => {
										// this.triggerEvent('success', res.tempFilePath);
										this.$emit('success', res.tempFilePath)
									},
									fail: (err) => {
										// this.triggerEvent('fail', err);
										this.$emit('fail', err)
									},
								}, this);
							}, time);
						});
					})
					.catch((err) => {
						console.log(123)
						wx.showToast({
							icon: 'none',
							title: err.errMsg || '生成失败'
						});
						console.error(err);
					});
			},
			trigger(event, data) {
				if (this.listener && typeof this.listener[event] === 'function') {
					this.listener[event](data);
				}
			},
			once(event, fun) {
				if (typeof this.listener === 'undefined') {
					this.listener = {};
				}
				this.listener[event] = fun;
			},
			downloadResource(reset) {
				return new Promise((resolve, reject) => {
					if (reset) {
						this.downloadStatus = null;
					}
					const poster = this.selectComponent('#poster');
					if (this.downloadStatus && this.downloadStatus !== 'fail') {
						if (this.downloadStatus === 'success') {
							resolve();
						} else {
							this.once('downloadSuccess', () => resolve());
							this.once('downloadFail', (e) => reject(e));
						}
					} else {
						poster.downloadResource(this.config)
							.then(() => {
								this.downloadStatus = 'success';
								resolve();
							})
							.catch((e) => reject(e));
					}
				})
			},
			onCreate(reset = false) {
				!this.hideLoading && wx.showLoading({
					mask: true,
					title: '生成中'
				});
				return this.downloadResource(typeof reset === 'boolean' && reset).then(() => {
						!this.hideLoading && wx.hideLoading();
						const poster = this.selectComponent('#poster');
						poster.create(this.config);
					})
					.catch((err) => {
						!this.hideLoading && wx.hideLoading();
						console.log(456)
						wx.showToast({
							icon: 'none',
							title: err.errMsg || '生成失败'
						});
						console.error(err);
						this.triggerEvent('fail', err);
					})
			},
			onCreateSuccess(e) {
				const {
					detail
				} = e;
				// this.triggerEvent('success', detail);
				this.$emit('success', detail)
			},
			onCreateFail(err) {
				console.error(err);
				// this.triggerEvent('fail', err);
				this.$emit('fail', err)
			}
		}, main, handle, helper)
	}
</script>

<style lang="less">
.canvas {
    width: 750rpx;
    height: 750rpx;
}
.canvas.pro {
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translate3d(-9999rpx, 0, 0);
}
.canvas.debug {
    position: absolute;
    bottom: 0;
    left: 0;
    border: 1rpx solid #ccc;
}
</style>
